<template>
  <div class="hello">
 			<input type="text"  v-model="name"/>
 			<button @click="add">添加</button>
		<ul>
			<li v-for="item in list">{{item.name}}<button @click="del(item.id)">删除</button></li>
		</ul>
  </div>
</template>

<script>
export default {
  	data(){
  		return {
  			list :[],
  			name : ''
  		}
  	},
  	created(){
  		this.getData();
  	},
  	watch :{
  		name(n){
  			this.find(n)
  		}
  	},
  	methods :{
  		getData(){
  			this.$http.get("http://localhost:3000/list").then((res)=>{
  				this.list=res.data
  			})
  		},
  		find(kw){
  			this.$http.get("http://localhost:3000/list?name_like="+kw).then((res)=>{
  				this.list=res.data
  			})
  		},
  		add(){
  			this.$http.post("http://localhost:3000/list",{
  				name : this.name
  			},{
  				headers:{
  					"content-type":"application/json"
  				}
  			}).then((res)=>{
  				this.getData();
  			})
  		},
  		del(id){
  			this.$http.delete("http://localhost:3000/list/"+id).then((res)=>{
  				this.getData();
  			})
  		}
  	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



